.dialogItems {
    padding: 10px;
    display: grid;
    grid-template-columns: 2fr 10fr;
}
.dialogs .dialog {
    padding: 10px;
}

.messages .message {
    padding: 10px;
}

.active {
    color: gold;
}

.dialog a {
    text-decoration: none;
}
.dialogPhoto img {
    width: 70px;
    height: 70px;
    border-radius: 30px;
}